.not-found {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.not-found .error-message {
    font-size: 1.5rem;
    color: rgb(70, 70, 70);
    text-align: center;
    display: flex;
    align-items: center;
    gap: 20px;
}

.not-found .error-message .main-message {
    font-size: 2rem;
    font-weight: bold;
}

